.btn {
  width: 100%;
  font-size: 1rem;
  border: 1px solid #505050;

  padding: .5rem;  
  border-radius: 7px;
  background: none;
  color: #ff9633;
  border-color: #ff9633;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;

  &:hover {
    opacity: .8;
    cursor: pointer;
    background: #ff9633;
    color: #fcfcfc;
  }

  &:active {
    transform: translateY(3%) scale(0.97);
  }
}
.singleInput {
  border-radius: 0 7px 7px 0;
  border-color: #00a850;
  background: #00a850;
  color: #fcfcfc;
}

.popin {
  -webkit-animation: pop-in .2s ease-in-out;
          animation: pop-in .2s ease-in-out;
}

@media screen and (max-width: 725px) {
  .btn {
    font-size: .8rem;
  }
}

