.form {
  width: 80vw;
  display: -ms-grid;
  display: grid;
  margin-bottom: .75rem;

  -ms-grid-rows: auto auto;
  grid-template-rows: repeat(2, auto);
  -ms-grid-columns: 5fr 1fr;
  grid-template-columns: 5fr 1fr;

  label {
    -ms-grid-row: 1;
    -ms-grid-row-span: 1;
    -ms-grid-column: 1;
    -ms-grid-column-span: 3;
    grid-area: 1/1/2/4;
    text-align: left;
    
    margin-bottom: .25rem;
    height: 100%;
    
    font-size: 1.5rem;
  }

  input {
    padding: .5rem 1rem;
    font-size: 1.2rem;
    color: var(--color-text);
    border: 1px solid #a0a0a0;
    border-radius: 7px 0 0 7px;

    background: var(--color-background-alt);
  }
}

@media screen and (max-width: 360px){
  .form {
    label {
      font-size: 1.2rem;
    }

    input {
      font-size: 1rem;
      padding: .25rem .5rem;
    }
  }
}